const $ = require('jquery');
const commdate = {
    local_time: function () {
        let local = new Date();
        let year = local.getFullYear();
        let month = local.getMonth() + 1;
        if (month < 10) {
            month = "0" + month;
        }
        let day = local.getDate();
        if (day < 10) {
            day = "0" + day;
        }
        let time = local.toTimeString().substr(0, 8);
        let local_time = year + "-" + month + "-" + day + " " + time;
        return local_time;
    },
    am_pm_data: function (ampm) {
        let year = ampm.substr(0, 4);
        let month = ampm.substr(5, 2);
        let day = ampm.substr(8, 2);
        let hour = ampm.substr(11, 2);
        let day_ampm;
        let ampmNew;
        if (hour < 12) {
            day_ampm = " ";
        } else if (hour == 12) {
            day_ampm = " ";
        } else if (hour > 12) {
            day_ampm = " ";
        }
        let minute = ampm.substr(14, 2);
        ampmNew = day_ampm + hour + ":" + minute + " " + year + "" + month + "" + day + "";
        return ampmNew;
    },
    cut_date_msec: function (java_date) {
        java_date = java_date.substr(0, 19);
        return java_date;
    },
    isSameDay: function (day1, day2) {
        return day1.substr(0, 10) == day2.substr(0, 10);
    },
    diffDay_one: function (dateStrBefore, dateStrAfter) {
        let dateStrBefore1 = dateStrBefore.substr(0, 19); //
        let dateStrAfter1 = dateStrAfter.substr(0, 19); //
        let dateStrBefore2 = dateStrBefore1.replace(/\-/g, '/');
        let dateStrAfter2 = dateStrAfter1.replace(/\-/g, '/');

        let days = 1000 * 60 * 60 * 24;
        let day1 = Date.parse(dateStrBefore2);
        let day2 = Date.parse(dateStrAfter2);
        if (isNaN(day1)) {
            return NaN;
        }
        if (isNaN(day2)) {
            return NaN;
        }
        let d = parseInt((day2 - day1) / days);
        if (d < 1) {//
            let dateStrB = new Date(dateStrBefore.replace(/\-/g, '/'));
            let dateStrA = new Date(dateStrAfter.replace(/\-/g, '/'));
            let A_B = (dateStrA.getTime() - dateStrB.getTime()) / 1000;
            if (A_B < 60) { //
                return "";//A_B+"";
            } else {
                A_B = parseInt(A_B / 60);
                if (A_B < 60) {//
                    return A_B + "";
                } else {
                    A_B = parseInt(A_B / 60);
                    if (A_B < 60) {
                        return A_B + "";
                    }
                }
            }
        } else {
            if (d < 8) {
                return d + "";
            } else {
                return dateStrBefore.substr(0, 10); //
                //return dateStrBefore; //
            }

        }
    },
    diffDay_two: function (dateStrBefore, dateStrAfter) {
        let dateStrBefore1 = dateStrBefore.substr(0, 10);
        let dateStrAfter1 = dateStrAfter.substr(0, 10);

        let dateStrBefore2 = dateStrBefore1.replace(/\-/g, '/');
        let dateStrAfter2 = dateStrAfter1.replace(/\-/g, '/');

        let days = 1000 * 60 * 60 * 24;
        let day1 = Date.parse(dateStrBefore2);
        let day2 = Date.parse(dateStrAfter2);
        if (isNaN(day1)) {
            //alert(dateStrBefore + "");
            return NaN;
        }
        if (isNaN(day2)) {
            //alert(dateStrAfter + "");
            return NaN;
        }
        let d = (day2 - day1) / days;
        if (d > 7) {
            //return dateStrBefore; //
            return dateStrBefore.substr(0, 10);//
        } else {
            return "7";
        }
    },
    //
    diffDay_three: function (dateStrBefore, dateStrAfter) {
        let dateStrBefore1 = dateStrBefore.substr(0, 19); //
        let dateStrAfter1 = dateStrAfter.substr(0, 19); //
        let dateStrBefore2 = dateStrBefore1.replace(/\-/g, '/');
        let dateStrAfter2 = dateStrAfter1.replace(/\-/g, '/');

        let beforeYear = dateStrBefore1.substring(0,4);
        let beforeMonth = dateStrBefore1.substring(5,7);
        let beforeDay = dateStrBefore1.substring(8,10);

        let afterYear = dateStrAfter1.substring(0,4);
        let afterMonth = dateStrAfter1.substring(5,7);
        let afterDay = dateStrAfter1.substring(8,10);

        if(beforeYear==afterYear){//
            if(beforeMonth==afterMonth){//
                let day = parseInt(afterDay-beforeDay);//
                if(day==0){//
                    let days = 1000 * 60 * 60 * 24;
                    let day1 = Date.parse(dateStrBefore2);
                    let day2 = Date.parse(dateStrAfter2);
                    if (isNaN(day1)) {
                        //alert(dateStrBefore + "");
                        return NaN;
                    }
                    if (isNaN(day2)) {
                        //alert(dateStrAfter + "");
                        return NaN;
                    }
                    let d = parseInt((day2 - day1) / days);
                    if (d < 1) {//
                        let dateStrB = new Date(dateStrBefore.replace(/\-/g, '/').substring(0,19));
                        let dateStrA = new Date(dateStrAfter.replace(/\-/g, '/').substring(0,19));
                        let A_B = (dateStrA.getTime() - dateStrB.getTime()) / 1000;
                        if (A_B < 60) { //
                            return "";//A_B+"";
                        } else {
                            A_B = parseInt(A_B / 60);
                            if (A_B < 60) {//
                                return A_B + "";
                            } else {
                                A_B = parseInt(A_B / 60);
                                if (A_B < 60) {
                                    return A_B + "";
                                }
                            }
                        }
                    }
                }else if(day==1){//
                    return ' '+dateStrBefore.substr(11,5);
                }else if(day==2){
                    return ' '+dateStrBefore.substr(11,5);
                }else{
                    return dateStrBefore.substr(5,11).replace(/\-/g, '.');
                }
            }else{
                return dateStrBefore.substr(5,11).replace(/\-/g, '.');
            }
        }else{
            return dateStrBefore.substr(0, 10).replace(/\-/g, '.'); //
        }
    },
    thirdRule:function(dateStrBefore, dateStrAfter){  //
        //
        let dateStrBefore1 = dateStrBefore.substr(0,19); //
        let dateStrAfter1 = dateStrAfter.substr(0,19); //
        let dateStrBefore2 = dateStrBefore1.replace(/\-/g, '/');
        let dateStrAfter2 = dateStrAfter1.replace(/\-/g, '/');

        let days = 1000 * 60 * 60 * 24;
        let day1 = Date.parse(dateStrBefore2);
        let day2 = Date.parse(dateStrAfter2);
        if (isNaN(day1)) {
            //alert(dateStrBefore + "");
            return NaN;
        }
        if (isNaN(day2)) {
            //alert(dateStrAfter + "");
            return NaN;
        }
        let d = parseInt((day2 - day1) / days);
        if(d<1){//
            return "";
        }else{
            if(d<8){
                return d+"";
            }else{
                let year = commdate.local_time().split("-")[0];
                let pushYear = dateStrBefore.split("-")[0];
                if(year==pushYear){
                    return dateStrBefore.substring(dateStrBefore.indexOf("-")+1,10); //
                    //return dateStrBefore; //
                }else{
                    return dateStrBefore.substr(0,10); //
                    //return dateStrBefore; //
                }
            }

        }
    },
    date_word: function (a) {
        let year = a.substr(0, 4);
        let month = a.substr(5, 2);
        let day = a.substr(8, 2);
        if (month < 10) {
            month = month.substr(1, 1);
        }
        if (day < 10) {
            day = day.substr(1, 1);
        }
        a = year + "" + month + "" + day + "";
        return a;
    },
    datafomat: function (tar) {
        let obj = tar;
        let text;
        obj.each(function (i) {
            text = date_word(obj.eq(i).html());
            obj.eq(i).html(text.substring(text.indexOf('') + 1));
        });
    },
    dateJoint: function (date1, date2, sign, middleJoint) {//date1date2
        let result;
        let signal = (sign != undefined && sign != "") ? sign : '.';					//
        let mJoint = (middleJoint != undefined && middleJoint != "") ? middleJoint : "-";
        let d1 = date1.substring(0, 10).replace(/-/g, signal);
        let d1Arr = d1.toString().split(signal);
        let d2 = date2.substring(0, 10).replace(/-/g, signal);
        let d2Arr = d2.toString().split(signal);
        if (parseInt(d2Arr[0]) > parseInt(d1Arr[0])) {//
            result = d1 + mJoint + d2;
        } else {//
            if (parseInt(d2Arr[1]) > parseInt(d1Arr[1])) {//
                result = d1 + mJoint + d2.substring(5, 10);
            } else {
                result = d1 + mJoint + d2.substring(8, 10);
            }
        }
        return result;
    },
    vsLocalTime: function (time) {// 2016-10-18 23:20
        let t;
        if (time.indexOf(':') < 13) {
            t = (new Date().getFullYear() + "-" + time).substring(0, 16);
        } else {
            t = time.substring(0, 16);
        }
        let now = new Date().getTime();
        let thatTime = Date.UTC(t.substring(0, 4), parseInt(t.substring(5, 7)) - 1, t.substring(8, 10), parseInt(t.substring(11, 13)) - 8, t.substring(14, 16), 0);	//-1; 8
        let tSu = (thatTime - now) / 1000;//
        if (tSu > 0) {
            let _d = tSu / (60 * 60 * 24);//
            let _a = tSu / (60 * 60);	//
            let _m = tSu / 60;		//
            if (_d > 1) {
                return parseInt(_d) + "";
            } else if (_a > 1) {
                return parseInt(_a) + '';
            } else if (_m > 1) {
                return parseInt(_m) + ''
            } else {
                return "";
            }
        } else {
            let _d = tSu / (60 * 60 * 24);//
            let _a = tSu / (60 * 60);	//
            let _m = tSu / 60;		//
            if (_d < -1) {
                return -parseInt(_d) + "";
            } else if (_a < -1) {
                return -parseInt(_a) + '';
            } else if (_m < -1) {
                return -parseInt(_m) + '';
            } else {
                return "";
            }
        }

    },
    dateLocalJoint: function (date1, date2, sign, middleJoint,isMeeting) {//date1date2,signmiddleJoint
        let result;
        let signal = (sign != undefined && sign != "") ? sign : '.';					//
        let mJoint = (middleJoint != undefined && middleJoint != "") ? middleJoint : "-";
        let d1 = date1.substring(0, 10).replace(/-/g, signal);
        let d1Arr = d1.toString().split(signal);
        let d2 = date2.substring(0, 10).replace(/-/g, signal);
        let d2Arr = d2.toString().split(signal);
        if (parseInt(d2Arr[0]) > parseInt(d1Arr[0])) {//
            result = d1 + mJoint + d2;
        } else {//
            if(new Date().getFullYear()==parseInt(d1Arr[0])){//
                if (Date.parse(new Date(d1))==Date.parse(new Date(d2))) {//
                    if(isMeeting){//
                        result = d1.substring(5, 10) + mJoint + d2.substring(5, 10);
                    }else{
                        result = date1.substring(5, 10);
                    }
                } else {
                    result = d1.substring(5, 10) + mJoint + d2.substring(5, 10);
                }
            }else{
                if (Date.parse(new Date(d1))==Date.parse(new Date(d2))) {//
                    if(isMeeting){//
                        result = d1 + mJoint + d2;
                    }else{
                        result = date1.substring(0,10);
                    }
                } else {
                    result = d1 + mJoint + d2;
                }
            }
        }
        return result;
    }
};
export default commdate;
