<template>
    <!---->
    <section class="alEmr-belongPop" v-if="deleteBtn||passDelete">
        <span class="deleteTips" :class="{redTips:forbidDelete||caseEdit,greenTips:passDelete}"><i v-if="passDelete"></i>{{textTips}}</span>
        <p class="firstBtn" v-if="!passDelete" @click="deCancel"></p>
        <p class="secBtn wid116"  v-if="!passDelete" @click="deSure"></p>
    </section>
    <!---->
</template>

<script>
    import {mapActions,mapGetters} from 'vuex';
    import comm from "~utils/comm.js";
    const deleteUrl="/call/caseFolder/baseinfo/update/";
    export default {
        data(){
            return{
                textTips:"",//
                textFlag:false,//
            }
        },
        name: 'belongOpPop',
        computed:{
            ...mapGetters(['deleteBtn','loading','deleteId','forbidDelete','passDelete','caseEdit','itemsArr']),
            textFn(){
                let t=this;
                if (!(t.forbidDelete||t.passDelete||t.caseEdit)){//
                    return t.textFlag=true;
                }
            }
        },
        watch:{
            forbidDelete(){//
                let t=this;
                if(t.forbidDelete==true){//
                    t.textTips="";
                }else{
                    t.textTips="";
                }
            },
            passDelete(){//
                let t=this;
                if(t.passDelete==true){//
                    t.textTips="";
                }else{
                    t.textTips="";
                }
            },
            caseEdit(){//
                let t=this;
                if(t.caseEdit==true){//
                    t.textTips="";
                }else{
                    t.textTips="";
                }
            },
            textFlag(){//
                let t=this;
                if(t.textFlag==true){//
                    t.textTips="";
                }
            }
        },
        methods: {
            ...mapActions(['loadingChange','caseListInit','deCancel','deleteStatus','forbidDeleteChange',
                'caseEditChange','passDeleteChange','itemsArrChange']),
            //
            deSure(){
                let t=this;
                if(t.loading){
                    return;
                }
                if(!t.deleteId){
                    return;
                }
                t.loadingChange(true);
                comm.ajax2({
                    url:deleteUrl,
                    type:"post",
                    dataType:"json",
                    data:{paramJson:JSON.stringify({
                            customerId:comm.cookie.get("userId")?comm.cookie.get("userId"):"",
                            visitSiteId:"1",
                            updateFlag:"4",
                            lockedFlag:"1",
                            caseId:t.deleteId,//'1532756363101,1531999302226,',
                            isValid:"0"
                        })
                    },
                    success:function(res) {
                        t.loadingChange(false);
                        if(res.responseObject.responseStatus) {//
                            let _code=res.responseObject.responseCode;
                            if(_code==0){
                                /**/
                                for (let k in t.itemsArr) {
                                    if(t.itemsArr[k].caseId==t.deleteId){
                                        t.itemsArr.splice(k,1);
                                        break;
                                    }
                                }
                                t.itemsArrChange(t.itemsArr);
                                /**/
                                t.deCancel();//
                                t.passDeleteChange(true);//
                                t.forbidDeleteChange(false);//,
                                t.caseEditChange(false);//

                                setTimeout(function() {
                                    t.passDeleteChange(false);//

                                    t.caseListInit(true);//
                                },2000);
                            }else if(_code==1301){//
                                t.caseEditChange(true);//
                                t.passDeleteChange(false);//
                                t.forbidDeleteChange(false);//,
                                t.deleteStatus();//
                            }else if(_code==1303){//
                                t.forbidDeleteChange(true);//,
                                t.caseEditChange(false);//
                                t.passDeleteChange(false);//
                                t.deleteStatus();//
                            }
                        }
                    }
                });
            },
        }
    }
</script>

