<template>
    <!---->
    <section class="alEmr-sucPop" v-if="teamFailPerson">
        <section class="sucText">
            <aside>
                <i class="failTips"></i>
                <p>
                    <span class="textBelong">{{teamName}}</span>
                </p>
            </aside>
        </section>
    </section>
    <!---->
</template>

<script>
    import {mapActions,mapGetters} from 'vuex';
    export default {
        name: 'belongFailPop',
        data(){
            return{
                teamName:'',//
            }
        },
        computed:{
            ...mapGetters(['teamFailPerson','teamNameList']),
        },
        watch:{
            'teamNameList':{
                handler(v1,v2){
                    let t=this;
                    for (let i=0;i<t.teamNameList.length;i++){
                        let items=t.teamNameList[i];
                        if(items.active){//
                            t.teamName=items.teamName;
                        }
                    }
                },
                deep:true
            }
        }
    }
</script>

<style scoped>

</style>
